@mixin alert() {
  padding : 15px;
  border-radius : 5px;
  width : 100%;
  margin : auto;

  p {
    margin-bottom : 0;
  }
}
.my-alert {
  @include alert();
  background : #eeeeee;
}
.my-alert2 {
  @include alert();
  background : #ed8;
} 

.fade-in-enter {
  opacity: 0;
}
.fade-in-enter-active {
  opacity: 1;
  transition: all .5s;
}